export default{
	props:["reachbottom","pulldown"],//两个函数（加载，刷新）
	data(){
		return {
			offset: 0,
			onTheTop:true,
			touchSpot: {
				startX: 0,
				startY: 0
			 },
		}
	},
	methods:{
		bindscrolltolower: function (e) {
		      if ( this.lock) return;//可以从父组件传值统一控制上拉加载结束
		      this.lock = true;
			  this.reachbottom({//上拉加载
				stop: () => {
				  this.lock = false;
				}
			  });
		     
		    },
		bindscrolltoupper: function (e) {
		  this.onTheTop = true;
		},
		bindscroll: function (e) {
		  if (e.detail.scrollTop === 0) {
			this.onTheTop = true;
		  } else {
			this.onTheTop = false;
		  }
		},
		touchstart(e) {
		  if (!this.onTheTop || !e.changedTouches[0]) return;
		  const { pageX, pageY } = e.changedTouches[0];
		  this.touchSpot.startX = pageX;
		  this.touchSpot.startY = pageY;
		},
		touchmove(e) {
		  //return//关闭下拉刷新
		  if (!this.onTheTop || !e.changedTouches[0]) {
			return;
		  } else if (!this.touchSpot.startX) {
			const { pageX, pageY } = e.changedTouches[0];
			this.touchSpot.startX = pageX;
			this.touchSpot.startY = pageY;
			
			return;
		  }
		  const { pageX, pageY } = e.changedTouches[0];
		  let offset = (pageY - this.touchSpot.startY) / 2;
		  this.offset=offset > 100 ? 100 : offset;
		
		},
		touchend(e) {
		  // return
		  if (this.offset >= 70) {
			if (this.offset < 100) {
				this.offset=100;
			}
			 // 下拉刷新
			this.pulldown({
				stop:()=>{
					setTimeout(() => {
					  this.offset=0
					}, 600);
				}
			})
		   
		  } else {
			  this.offset=0
		  }
		  this.resetTouchSpot();
		},
		resetTouchSpot() {
		  this.touchSpot = {
			startX: 0,
			startY: 0
		  };
		}
	}
}
